  background-repeat: no-repeat;
}

.pool-item .token-balance {
  flex: 2 1;
  font-weight: bold;
  text-align: right;
  padding: 0 24px 0 0;
}

.option-wrapper {
  display: flex;
}

.option-icon {
  width: 24px;
  height: 24px;
  margin-right: 12px;
  background-position: center;
  background-size: 100% auto;
  background-repeat: no-repeat;
}

.icon-pWBTC {
  background-image: url(/static/media/pWBTC.d07e5520.svg);
}

.icon-ONTd {
  background-image: url(/static/media/ONTd.91cafcfb.svg);
}

.icon-prenBTC {
  background-image: url(/static/media/prenBTC.723a54df.svg);
}

.icon-pUSDC {
  background-image: url(/static/media/pUSDC.36389a99.svg);
}

.icon-WING {
  background-image: url(/static/media/wing_icon.5c82c4dd.svg);
}

.icon-pETH {
  background-image: url(/static/media/ETH.a780c762.svg);
}

.icon-pDAI {
  background-image: url(/static/media/oDAI.4b258e31.svg);
}

.icon-pUSDT {
  background-image: url(/static/media/pusdt.24c032de.svg);
}

.icon-psUSD {
  background-image: url(/static/media/psusd.4682e7f3.svg);
}

.icon-pNEO {
  background-image: url(/static/media/neo-ont.c7dce7b8.svg);
}

.icon-pUNI {
  background-image: url(/static/media/UNI-ONT.5126eab5.svg);
}

.icon-ONG {
  background-image: url(/static/media/ONG_icon.bdb6010a.svg);
}

.icon-ONT {
  background-image: url(/static/media/ONT.d4414398.svg);
}

.icon-UNX {
  background-image: url(/static/media/newunx.a65cca28.svg);
}

.option-label {
  flex: 1 1;
  padding: 2px 0;
}
.stake-container {
  position: relative;
  width: 1200px;
  margin: 20px -100px 0;
}

.stake-pool .title {
  height: 200px;
  background: url(/static/media/stake.70a00d8a.jpg) center center / auto 100% no-repeat;
}

.pool-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 40px -10px 0;
}

.pool-list-item {
  width: 32%;
  box-sizing: border-box;
  padding: 0 20px;
  margin-bottom: 40px;
  background-position: center 20px;
  background-size: 48px auto;
  background-repeat: no-repeat;
}

.item-detail .token-name {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 10px;
}

.token-balance {
  padding-bottom: 20px;
}

.token-weight {
  padding-bottom: 20px;
