}

.interest-rate {
  padding-bottom: 20px;
}

.item-detail {
  padding: 120px 24px 24px;
  font-size: 16px;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 0 0 20px rgba(0, 0, 0, .07);
  background-color: #fff;
