  font-weight: bold;
  line-height: 48px;
  cursor: pointer;
  color: #fff;
  border-radius: 40px;
  background: RGBA(43, 190, 127, 1.00);
}

/* deposit modal */

.stake-wrapper {
  position: relative;
  width: 400px;
  height: 500px;
}

.selected-token {
  flex: 1 1;
  height: 38px;
  line-height: 38px;
  padding-left: 48px;
  background-position: 12px center;
  background-size: 24px auto;
  background-repeat: no-repeat;
}

.stake-tabs {
