  cursor: pointer;
  border-bottom: 4px solid transparent;
}

.stake-tab-item.selected {
  color: #2c2c2c;
  border-color: #2c2c2c;
}

.token-placeholder {
  height: 140px;
  background-position: center 40px;
  background-size: 64px auto;
  background-repeat: no-repeat;
}

.stake-wrapper .input {
  padding: 18px 12px;
  font-size: 18px;
}

.stake-btn {
  position: absolute;
  bottom: 40px;
  right: 24px;
  left: 24px;
  font-size: 24px;
  font-weight: bold;
  line-height: 48px;
  text-align: center;
  border-radius: 40px;
  color: #fff;
  cursor: pointer;
  background: #2c2c2c;
}


.coming-soon-placeholder {
  height: 400px;
  background: url(/static/media/coming.341c7b28.jpg) center / auto 100% no-repeat;
}
.governance-wrapper {
  padding-top: 20px;
}

.overview-wrapper {
  padding: 0 0 20px;
